'use client'

import { useState, useRef, useEffect } from 'react'
import { ChatMessage, Message } from './components/ChatMessage'
import { ChatInput } from './components/ChatInput'
import { ToolExecution, ToolExecutionItem, ThoughtItem } from './components/ToolExecution'
import { PptxViewer } from './components/PptxViewer'
import { ImageUpload } from './components/ImageUpload'

interface TracingSummary {
  totalTokens: number
  inputTokens: number
  outputTokens: number
  totalCost: number
}

interface WaitingFeedback {
  question: string
  pptxPath?: string
  backgroundPath?: string
}

export default function Home() {
  const [messages, setMessages] = useState<Message[]>([])
  const [isLoading, setIsLoading] = useState(false)
  const [toolExecutions, setToolExecutions] = useState<ToolExecutionItem[]>([])
  const [thoughts, setThoughts] = useState<ThoughtItem[]>([])
  const [currentPptx, setCurrentPptx] = useState<string | null>(null)
  const [uploadedImage, setUploadedImage] = useState<string | null>(null)
  const [sessionId, setSessionId] = useState<string | null>(null)
  const [waitingFeedback, setWaitingFeedback] = useState<WaitingFeedback | null>(null)
  const [tracingSummary, setTracingSummary] = useState<TracingSummary | null>(null)
  const messagesEndRef = useRef<HTMLDivElement>(null)

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' })
  }

  useEffect(() => {
    scrollToBottom()
  }, [messages, toolExecutions, thoughts])

  const handleSendMessage = async (content: string) => {
    const userMessage: Message = {
      id: Date.now().toString(),
      role: 'user',
      content,
      timestamp: new Date(),
      image: uploadedImage || undefined,
    }

    setMessages((prev) => [...prev, userMessage])
    setIsLoading(true)
    setToolExecutions([])
    setThoughts([])
    setWaitingFeedback(null)
    setTracingSummary(null)
    setUploadedImage(null)

    try {
      const response = await fetch('/api/agent', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          prompt: content,
          imageBase64: uploadedImage,
          sessionId,
        }),
      })

      if (!response.ok) throw new Error('Agent request failed')

      const reader = response.body?.getReader()
      const decoder = new TextDecoder()

      if (!reader) throw new Error('No response body')

      let buffer = ''
      while (true) {
        const { done, value } = await reader.read()
        if (done) break

        buffer += decoder.decode(value, { stream: true })
        const lines = buffer.split('\n')
        buffer = lines.pop() || ''

        for (const line of lines) {
          if (!line.trim()) continue

          try {
            const event = JSON.parse(line)
            handleStreamEvent(event)
          } catch {
            // Skip non-JSON lines
          }
        }
      }
    } catch (error) {
      console.error('Error:', error)
      const errorMessage: Message = {
        id: Date.now().toString(),
        role: 'assistant',
        content: '„Ç®„É©„Éº„ÅåÁô∫Áîü„Åó„Åæ„Åó„Åü„ÄÇ„ÇÇ„ÅÜ‰∏ÄÂ∫¶„ÅäË©¶„Åó„Åè„Å†„Åï„ÅÑ„ÄÇ',
        timestamp: new Date(),
      }
      setMessages((prev) => [...prev, errorMessage])
    } finally {
      setIsLoading(false)
    }
  }

  const handleStreamEvent = (event: {
    type: string
    sessionId?: string
    tool?: string
    status?: 'running' | 'completed' | 'error'
    result?: string
    input?: string
    content?: string
    pptxPath?: string
    previewPath?: string
    backgroundPath?: string
    question?: string
    totalTokens?: number
    inputTokens?: number
    outputTokens?: number
    totalCost?: number
  }) => {
    switch (event.type) {
      case 'session_start':
        setSessionId(event.sessionId || null)
        break

      case 'thought':
        if (event.content) {
          setThoughts((prev) => [
            ...prev,
            {
              id: Date.now().toString(),
              content: event.content!,
              timestamp: new Date(),
            },
          ])
        }
        break

      case 'tool_start':
        setToolExecutions((prev) => [
          ...prev,
          {
            id: Date.now().toString(),
            name: event.tool || 'unknown',
            status: 'running',
            startTime: new Date(),
            input: event.input,
          },
        ])
        break

      case 'tool_end':
        setToolExecutions((prev) =>
          prev.map((t) =>
            t.name === event.tool && t.status === 'running'
              ? {
                  ...t,
                  status: event.status || 'completed',
                  result: event.result,
                  endTime: new Date(),
                }
              : t
          )
        )
        break

      case 'waiting_feedback':
        setWaitingFeedback({
          question: event.question || '',
          pptxPath: event.pptxPath,
          backgroundPath: event.backgroundPath,
        })
        // „Éï„Ç£„Éº„Éâ„Éê„ÉÉ„ÇØÂæÖ„Å°„ÅÆÊôÇÁÇπ„ÅßPPTX„Éë„Çπ„ÇíÊõ¥Êñ∞
        if (event.pptxPath) {
          setCurrentPptx(event.pptxPath)
        }
        break

      case 'message':
        const assistantMessage: Message = {
          id: Date.now().toString(),
          role: 'assistant',
          content: event.content || '',
          timestamp: new Date(),
        }
        setMessages((prev) => [...prev, assistantMessage])
        break

      case 'error':
        const errorMsg: Message = {
          id: Date.now().toString(),
          role: 'assistant',
          content: event.content || '„Ç®„É©„Éº„ÅåÁô∫Áîü„Åó„Åæ„Åó„Åü„ÄÇ',
          timestamp: new Date(),
        }
        setMessages((prev) => [...prev, errorMsg])
        break

      case 'pptx_generated':
        if (event.pptxPath) {
          setCurrentPptx(event.pptxPath)
        }
        setWaitingFeedback(null)
        break

      case 'tracing_summary':
        setTracingSummary({
          totalTokens: event.totalTokens || 0,
          inputTokens: event.inputTokens || 0,
          outputTokens: event.outputTokens || 0,
          totalCost: event.totalCost || 0,
        })
        break

      case 'done':
        setWaitingFeedback(null)
        break
    }
  }

  const handleImageUpload = (base64: string) => {
    setUploadedImage(base64)
  }

  const handleImageRemove = () => {
    setUploadedImage(null)
  }

  return (
    <div className="flex h-screen">
      {/* Chat Section */}
      <div className="flex flex-col flex-1 border-r border-[var(--border)]">
        {/* Header */}
        <header className="flex items-center justify-between px-6 py-4 border-b border-[var(--border)]">
          <h1 className="text-xl font-bold">NanoBanana PPTX Generator</h1>
          <div className="text-sm text-[var(--muted)]">
            {sessionId && <span>Session: {sessionId}</span>}
          </div>
        </header>

        {/* Messages */}
        <div className="flex-1 overflow-y-auto p-6 space-y-4">
          {messages.length === 0 && !isLoading && (
            <div className="flex flex-col items-center justify-center h-full text-center text-[var(--muted)]">
              <div className="text-6xl mb-4">üçå</div>
              <h2 className="text-xl font-semibold mb-2">„Éó„É¨„Çº„É≥„ÉÜ„Éº„Ç∑„Éß„É≥„Çí‰ΩúÊàê„Åó„Åæ„Åó„Çá„ÅÜ</h2>
              <p>‰ΩúÊàê„Åó„Åü„ÅÑ„Çπ„É©„Ç§„Éâ„ÅÆÂÜÖÂÆπ„ÇíÂÖ•Âäõ„Åó„Å¶„Åè„Å†„Åï„ÅÑ„ÄÇ</p>
              <p className="text-sm mt-2">ÂèÇÁÖßÁîªÂÉè„Çí„Ç¢„ÉÉ„Éó„É≠„Éº„Éâ„Åô„Çã„Åì„Å®„ÇÇ„Åß„Åç„Åæ„Åô„ÄÇ</p>
            </div>
          )}

          {messages.map((message) => (
            <ChatMessage key={message.id} message={message} />
          ))}

          {/* Tool Executions & Thoughts */}
          {(toolExecutions.length > 0 || thoughts.length > 0) && (
            <ToolExecution
              executions={toolExecutions}
              thoughts={thoughts}
              waitingFeedback={waitingFeedback}
              tracingSummary={tracingSummary}
            />
          )}

          {isLoading && toolExecutions.length === 0 && (
            <div className="flex items-center gap-2 text-[var(--muted)]">
              <div className="w-2 h-2 bg-[var(--primary)] rounded-full animate-pulse" />
              <span>„Ç®„Éº„Ç∏„Çß„É≥„Éà„ÇíËµ∑Âãï‰∏≠...</span>
            </div>
          )}

          <div ref={messagesEndRef} />
        </div>

        {/* Image Upload Preview */}
        {uploadedImage && (
          <div className="px-6 py-2 border-t border-[var(--border)]">
            <ImageUpload
              preview={uploadedImage}
              onRemove={handleImageRemove}
            />
          </div>
        )}

        {/* Input */}
        <div className="p-6 border-t border-[var(--border)]">
          <ChatInput
            onSend={handleSendMessage}
            onImageUpload={handleImageUpload}
            disabled={isLoading}
          />
        </div>
      </div>

      {/* PPTX Viewer Section */}
      <div className="w-[600px] flex flex-col bg-[#111]">
        <div className="px-6 py-4 border-b border-[var(--border)]">
          <h2 className="text-lg font-semibold">„Éó„É¨„Éì„É•„Éº</h2>
        </div>
        <div className="flex-1 overflow-y-auto p-6">
          <PptxViewer
            pptxPath={currentPptx}
            sessionId={sessionId}
          />
        </div>
      </div>
    </div>
  )
}
